import React from 'react';
import './index.css';

function Title() {
  return <h1>Tutorial</h1>
}

function Square(props) {
  return (
    <button
      className={props.winMark? 'square square-win': 'square'}
      onClick={props.onClick}
    >
      {props.value}
    </button>
  );
}

function calculateWinner(squares) {
  const lines = [
    [0, 1, 2],
    [3, 4, 5],
    [6, 7, 8],
    [0, 3, 6],
    [1, 4, 7],
    [2, 5, 8],
    [0, 4, 8],
    [2, 4, 6]
  ];

  for (let i = 0; i < lines.length; i++) {
    const [a, b, c] = lines[i];

    if (squares[a].value && squares[a].value === squares[b].value && squares[a].value === squares[c].value) {
      // square
      return {
        winner: squares[a].value,
        winningLineIndexArray: [a, b, c]
      };
    }
  }

  return null;
}

function gamemIsDraw(squares) {
  const gameResult = calculateWinner(squares);

  // 
  const allSquareIsFilled = squares.every((item) => {
    return item.value;
  });

  // 9
  if (gameResult) {
    return gameResult;
  }

  // 9
  if (allSquareIsFilled) {
    return 'This is a draw!'
  }

  return null;
}

class Board extends React.Component {
  // n is the item index in an array
  renderSquare(n, location) {
    return (
        <Square
          key={n}
          value={this.props.squares[n].value}
          winMark={this.props.squares[n].winMark}
          onClick={() => this.props.onClick(n, location)}
        />
      );
  }

  render() {
    let n = 0;
    let board = [];
    let row;

    // render the 3x3 squares
    for (let i = 0; i < 3; i++) {
      row = [];
      for (let j = 0; j < 3; j++, n++) {
        let location = `(${i + 1}, ${j + 1})`;
        row.push(this.renderSquare(n, location));
      }
      board.push(<div className="board-row" key={i}>{row}</div>);
    }

    return (
      <div className='board-pad'>{board}</div>
    );
  }
}

class Game extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      history: [{
        squares: Array(9).fill(null).map(() => ({value: null, winMark: false})),
        move: {
          player: null,
          location: null
        },
        when: Date.now()
      }],
      stepNumber: 0,
      xIsNext: true
    }
  }

  handleClick(i, location) {
    // Q1 A: slice()item

    // handleClick
    // time travel
    // time travel
    // stepNumberhistory
    // squaresconcathistory
    // stepNumbersquaresslicestepNumber + 1
    const history = this.state.history.slice(0 , this.state.stepNumber + 1);
    const current = history[history.length - 1];

    // squaresslice()slice()BooleanNumberString
    // ObjectJSON.parse()JSON.stringify()
    const squares =  JSON.parse(JSON.stringify(current.squares));

    // nullsetState()
    if (calculateWinner(squares) || squares[i].value) {
      return;
    }

    const nextPlayer = this.state.xIsNext? 'X': 'O';

    squares[i].value = nextPlayer;

    this.setState({
      history: history.concat([{
        squares,
        move: {
          player: nextPlayer,
          location: location
        },
        when: Date.now()
      }]),
      // stepNumberhistoryslice()squarehistory+1
      stepNumber: history.length,
      xIsNext: !this.state.xIsNext
    });
  };

  // stepNumberBoardsquares
  jumpTo(step) {
    this.setState({
      stepNumber: step,
      xIsNext: (step % 2) === 0
    });
  }

  render() {
    const history = this.state.history;
    //  stepNumber

    const current = history[this.state.stepNumber];
    const result = gamemIsDraw(current.squares);
    let gameIsEnd = false;

    // step movehistoryindex
    const moves = history.map((step, move) => {
      let stepStyle = 'step';

      if (move === this.state.stepNumber) {
        // --> 
        stepStyle += ' current-step';
      }

      // statehistory listitem
      const desc = move?
        `${step.move.player} moved to ${step.move.location}`:
        'Game start';

      return (
        <li key={step.when}>
          <button className={stepStyle} onClick={() => this.jumpTo(move)}>{desc}</button>
        </li>
      )
    });

    let status;
    const nextPlayer = this.state.xIsNext? 'X': 'O';

    if (typeof result === "string") {
      // The draw message
      status = result;
      gameIsEnd = true;
    } else if (result){
      // The winner info object
      status = 'Winner: ' + result.winner;

      // winMarkstateBoardstatewinMarkfalse
      // statewinMarkwinMark
      // statewinMarksquaresStringwinMarksquare
      result.winningLineIndexArray.forEach((item) => {
        current.squares[item].winMark = true;
      });

      gameIsEnd = true;
    } else {
      status = 'Next player: ' + nextPlayer;
    }

    return (
      <div className='game'>
        <div className='game-board'>
          <div className={gameIsEnd? 'game-is-end': ''}>{status}</div>
          <br/>
          <Board
            squares={current.squares}
            onClick={(i, location) => this.handleClick(i, location)}
          />
        </div>
        <div className="game-info">
          <div className='game-history-header'>Game History</div>
          <ol>{moves}</ol>
        </div>
      </div>
    );
  }
}

function Demo() {
  return (
    <div>
      <Title/>
      <hr/>
      <Game/>
    </div>
  )
}

export default Demo;


