import React from 'react';

import {ThemeContext, themes} from "./theme-context";
import ThemedButton from './themed-button';
import ThemeTogglerButton from './theme-toggler-button';

function Title() {
  return <h1>Context</h1>
}

// Example No.1
// propscontext
function Toolbar(props) {
  return (
    <div>
      <ThemedButton onClick={props.changeTheme}>
        Change Theme
      </ThemedButton>
    </div>
  );
}

class ToolbarWithThemedButton extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      theme: themes.light,
      toggleTheme: this.toggleTheme
    };
  }

  toggleTheme = () => {
    this.setState(state => ({
      theme: state.theme === themes.dark? themes.light: themes.dark
    }));
  };

  // ContextProviderthis.state.theme
  // propsthis.toggleThemestate
  render() {
    return (
      <div>
        <ThemeContext.Provider value={this.state.theme}>
          <Toolbar changeTheme={this.toggleTheme}/>
        </ThemeContext.Provider>
      </div>
    );
  }
}

// Example No.2
// context
function Content() {
  return (
    <div>
      <ThemeTogglerButton/>
    </div>
  )
}

class ContentWithThemedButton extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      theme: themes.light,
      toggleTheme: this.toggleTheme
    };
  }

  toggleTheme = () => {
    this.setState(state => ({
      theme: state.theme === themes.dark? themes.light: themes.dark
    }));
  };

  // toggleThemecontext
  // toggleThemestate
  // context
  render() {
    return (
      <div>
        <ThemeContext.Provider value={this.state}>
          <Content/>
        </ThemeContext.Provider>
      </div>
    );
  }
}


// Example No.3
// Context

// Theme context
const ThemeContext2 = React.createContext('light');

// Signed-in user context
const UserContext = React.createContext({
  name: 'Guest'
});

// A component consumes two contexts
function Content2() {
  return (
    <ThemeContext2.Consumer>
      {theme => (
        <UserContext.Consumer>
          {user => (
            <div user={user} theme={theme}/>
            //<ProfilePage user={user} theme={theme}/>
          )}
        </UserContext.Consumer>
      )}
    </ThemeContext2.Consumer>
  )
}

class ConsumeMutilpleContextsExample extends React.Component {
  render() {
    const {signedInUser, theme} = this.props;

    // It provides initial context values here.
    return (
      <div>
        <ThemeContext2.Provider value={theme}>
          <UserContext.Provider value={signedInUser}>
            <Content2/>
          </UserContext.Provider>
        </ThemeContext2.Provider>
      </div>
    );
  }
}


// Example No.4
//  props 
//  props 

class Button extends React.Component {
  componentDidMount() {
    // ThemeContext value is this.props.theme
  }

  componentDidUpdate(prevProps, prevState) {
    // Previous ThemeContext value is prevProps.theme
    // New ThemeContext value is this.props.theme
  }

  render() {
    const {theme, children} = this.props;
    return (
      <button className={theme? 'dark': 'light'}>
        {children}
      </button>
    )
  }
}


// Example No.5
//



function Demo() {
  return (
    <div>
      <Title/>
      <hr/>
      <ToolbarWithThemedButton/>
      <br/>
      <ContentWithThemedButton/>
    </div>
  )
}

export default Demo;



